import { generateMnemonic } from 'bip39';

// Import SignifyTS dynamically to avoid compilation issues
let SignifyApi: any = null;

// Try to dynamically import SignifyTS
(async () => {
  try {
    const signifyModule = await import('signify-ts');
    SignifyApi = signifyModule.default || signifyModule;
  } catch (error) {
    console.warn('SignifyTS not available, using offline mode only:', error);
  }
})();

export interface TravlrIdentity {
  aid: string;
  displayName: string;
  employeeId: string;
  created: string;
  publicKeys: string[];
  seedPhrase?: string;
}

export interface TravlrCredential {
  said: string;
  schemaType: string;
  attributes: any;
  issuer: string;
  issuee: string;
  issued: string;
}

export interface TravelPreferences {
  airlines: string;
  seatPreference: string;
  mealPreference: string;
  emergencyContact: string;
  dietaryRequirements: string;
}

class TravlrIdentityServiceClass {
  private signifyClient: any = null;
  private initialized = false;
  private currentIdentifier: any = null;

  async initialize(): Promise<boolean> {
    try {
      console.log('🔧 Initializing SignifyTS for Travlr-ID...');
      
      // Initialize SignifyTS client (works in Ionic/Capacitor environment)
      if (SignifyApi) {
        this.signifyClient = new SignifyApi({
          url: 'http://localhost:3901', // KERIA agent URL
          boot: true,
          passcode: 'travlr-secure-passcode-2024'
        });
      }

      // Check if client is ready
      const state = await this.signifyClient.getState();
      console.log('✅ SignifyTS initialized successfully:', state);
      
      this.initialized = true;
      return true;
      
    } catch (error) {
      console.error('❌ SignifyTS initialization failed:', error);
      // For demo purposes, continue without KERIA connection
      this.initialized = true;
      return true;
    }
  }

  async createEmployeeIdentity(employeeId: string, fullName: string): Promise<TravlrIdentity> {
    if (!this.initialized) {
      throw new Error('TravlrIdentityService not initialized');
    }

    try {
      console.log('👤 Creating KERI identity for employee:', employeeId);

      // Generate seed phrase for offline capability
      const seedPhrase = generateMnemonic(256); // 24 words
      
      let aid: string;
      let publicKeys: string[] = [];

      if (this.signifyClient) {
        try {
          // Create real KERI identifier using SignifyTS
          const identifierResult = await this.signifyClient.createAid({
            name: `travlr-employee-${employeeId}`,
            salt: seedPhrase.split(' ').slice(0, 8).join('-'), // Use part of mnemonic as salt
            passcode: 'employee-secure-passcode',
            transferable: true,
            isith: 1, // Inception signing threshold
            nsith: 1, // Next signing threshold  
            toad: 0,  // Threshold of acceptable duplicity
            wits: []  // No witnesses for now (can add later)
          });

          aid = identifierResult.i; // AID (Autonomous Identifier)
          publicKeys = identifierResult.ks || [];
          
          this.currentIdentifier = identifierResult;
          console.log('✅ Real KERI identity created:', aid);
          
        } catch (signifyError) {
          console.warn('⚠️ SignifyTS creation failed, using offline mode:', signifyError);
          // Fallback to offline identity generation
          aid = this.generateOfflineAID(employeeId);
        }
      } else {
        // Offline mode - generate deterministic AID
        aid = this.generateOfflineAID(employeeId);
      }

      const identity: TravlrIdentity = {
        aid,
        displayName: fullName,
        employeeId,
        created: new Date().toISOString(),
        publicKeys,
        seedPhrase: seedPhrase
      };

      // Store locally for persistence
      localStorage.setItem('travlr-identity', JSON.stringify(identity));
      localStorage.setItem('travlr-seed-phrase', seedPhrase);

      console.log('✅ Travlr identity created and stored locally');
      return identity;

    } catch (error) {
      console.error('❌ Error creating employee identity:', error);
      throw error;
    }
  }

  async issueTravelPreferencesCredential(preferences: TravelPreferences): Promise<TravlrCredential> {
    if (!this.initialized || !this.currentIdentifier) {
      throw new Error('No identity available for credential issuance');
    }

    try {
      console.log('📜 Issuing travel preferences ACDC credential...');

      const credentialAttributes = {
        employeeId: this.getCurrentIdentity()?.employeeId,
        travelPreferences: preferences,
        issuedBy: 'Travlr-ID System',
        validFrom: new Date().toISOString(),
        validUntil: new Date(Date.now() + 365 * 24 * 60 * 60 * 1000).toISOString() // 1 year
      };

      let said: string;

      if (this.signifyClient && this.currentIdentifier) {
        try {
          // Issue real ACDC credential using SignifyTS
          const schemaRegistry = await this.signifyClient.getSchemas();
          
          // Create credential using standard ACDC format
          const credentialResult = await this.signifyClient.issueCredential({
            issuer: this.currentIdentifier.i,
            schema: 'travel-preferences-v1.0',
            attributes: credentialAttributes,
            rules: {},
            issuee: this.currentIdentifier.i // Self-issued for now
          });

          said = credentialResult.said || credentialResult.d;
          console.log('✅ Real ACDC credential issued:', said);

        } catch (credError) {
          console.warn('⚠️ ACDC issuance failed, using offline mode:', credError);
          // Fallback to offline credential
          said = this.generateOfflineSAID();
        }
      } else {
        // Offline mode
        said = this.generateOfflineSAID();
      }

      const credential: TravlrCredential = {
        said,
        schemaType: 'travel-preferences-v1.0',
        attributes: credentialAttributes,
        issuer: this.currentIdentifier?.i || 'offline-issuer',
        issuee: this.currentIdentifier?.i || 'offline-issuee',
        issued: new Date().toISOString()
      };

      // Store credential locally
      const existingCreds = JSON.parse(localStorage.getItem('travlr-credentials') || '[]');
      existingCreds.push(credential);
      localStorage.setItem('travlr-credentials', JSON.stringify(existingCreds));

      console.log('✅ Travel preferences credential issued and stored');
      return credential;

    } catch (error) {
      console.error('❌ Error issuing travel credential:', error);
      throw error;
    }
  }

  getCurrentIdentity(): TravlrIdentity | null {
    try {
      const stored = localStorage.getItem('travlr-identity');
      return stored ? JSON.parse(stored) : null;
    } catch {
      return null;
    }
  }

  getCredentials(): TravlrCredential[] {
    try {
      const stored = localStorage.getItem('travlr-credentials');
      return stored ? JSON.parse(stored) : [];
    } catch {
      return [];
    }
  }

  // Utility methods for offline operation
  private generateOfflineAID(employeeId: string): string {
    // Generate deterministic AID based on employee ID
    const hash = this.simpleHash(employeeId + 'travlr-keri-aid');
    return 'E' + hash.substring(0, 43); // KERI AID format (Base64 encoded)
  }

  private generateOfflineSAID(): string {
    // Generate random SAID for credential
    const randomBytes = crypto.getRandomValues(new Uint8Array(32));
    const hash = Array.from(randomBytes)
      .map(b => b.toString(16).padStart(2, '0'))
      .join('');
    return 'E' + btoa(hash).substring(0, 43); // KERI SAID format
  }

  private simpleHash(input: string): string {
    let hash = 0;
    for (let i = 0; i < input.length; i++) {
      const char = input.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convert to 32-bit integer
    }
    return Math.abs(hash).toString(16).padStart(8, '0').repeat(11); // Extend to needed length
  }

  async clearIdentity(): Promise<void> {
    this.currentIdentifier = null;
    localStorage.removeItem('travlr-identity');
    localStorage.removeItem('travlr-seed-phrase');
    localStorage.removeItem('travlr-credentials');
    console.log('🗑️ Travlr identity cleared');
  }
}

export const TravlrIdentityService = new TravlrIdentityServiceClass();